.Footer{
    width: 100%;
    padding: 6rem 0;
    background-color: rgba(20, 20, 20, 0.7);
}
.Footer-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap:60px;
    padding-left: 1rem;
}
.Left{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem 1rem;

}
.Location{
    display: flex;
    /* margin-bottom: 0.8rem; */
}

.Right{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    /* padding: 1rem; */
}
.Social{
    margin-top: 1rem;
}

h4{
    font-size: 1.2rem;
    padding-bottom: 0.7rem;
}

@media  screen and (max-width: 640px) {
    .Footer-container{
        grid-template-columns: 1fr;
    }
}
